	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Wikipedia: March 11, 2004 Madrid attacks","date":"12 Mar 2004","slug":"/2004-03-12-wikipedia-march-11-2004-madrid-attacks/","tags":"Y2004"},"html":"<p><a title=\"ten bomb explosions that occurred aboard four cercanías (commuter trains) in Madrid\" href=\"http://en.wikipedia.org/wiki/March_11%2C_2004_Madrid_attacks\">Wikipedia: March 11, 2004 Madrid attacks</a></p>"},"next":{"fields":{"title":"Lyssa has moved","date":"12 Mar 2004","slug":"/2004-03-12-lyssa-has-moved/"}},"previous":{"fields":{"title":"Going Out With a Whimper","date":"14 Mar 2004","slug":"/2004-03-14-going-out-with-a-whimper/"}}},{"node":{"fields":{"title":"Lyssa has moved","date":"12 Mar 2004","slug":"/2004-03-12-lyssa-has-moved/","tags":"Y2004"},"html":"<p><a title=\"Blogrollers, update your links!\" href=\"http://www.lyssas-lounge.de/peepshow/\">Lyssa has moved</a></p>"},"next":{"fields":{"title":"Death in Madrid","date":"11 Mar 2004","slug":"/2004-03-11-death-in-madrid/"}},"previous":{"fields":{"title":"Wikipedia: March 11, 2004 Madrid attacks","date":"12 Mar 2004","slug":"/2004-03-12-wikipedia-march-11-2004-madrid-attacks/"}}},{"node":{"fields":{"title":"Death in Madrid","date":"11 Mar 2004","slug":"/2004-03-11-death-in-madrid/","tags":"Y2004"},"html":"<p>At 7:40 this morning my commuter train had pulled into the main station in Hamburg, and I was on my way to the U-Bahn to go work. At the same time in Madrid, <a title=\"Spain Struggles to Absorb Worst Terrorist Attack in Its History\" href=\"http://www.nytimes.com/2004/03/11/international/europe/11CND-TRAI.html?ex=1394427600&en=ff54915075ac4ed3&ei=5007&partner=USERLAND\">190 commuters in Madrid</a> were killed by at least 10 explosions at 3 commuter stations. I can only think of the words from the memorial service for the victims of 9/11. To paraphrase for todays events... this was not 190 people being killed, it was 1 person being killed, 1 person with home, family, friends, 1 person being killed.... 190 times.</p>\n<p>While I realize that it is necessary to find those responsible, be they from the ETA or al Qaeda, but on the other hand I find the speculation on which group is responsible to be somewhat cynical. Does it really matter to the victims for which supposed cause they were killed? Doesn't the attention given to the perpetrators and their motives give them a false legitimacy and encourage similar attacks in the future? At this moment, I would rather think of the father and husband who, unlike me, was not able to step off the train and onto the platform at 7:40 this morning, than about the twisted reasoning and motives of those who killed him.</p>"},"next":{"fields":{"title":"","date":"10 Mar 2004","slug":"/2004-03-10-koehler-fuer-deutschland/"}},"previous":{"fields":{"title":"Lyssa has moved","date":"12 Mar 2004","slug":"/2004-03-12-lyssa-has-moved/"}}}],"pathPrefix":"","first":false,"last":false,"index":511,"pageCount":1101}}

